<template>
  <div>
    <g-header></g-header>
    <div class="commentBox">
      <h4>评论详情</h4>
      <hr />
      <!-- 评论输入框模块 -->
      <van-cell-group>
        <van-field v-model="content" type="textarea" placeholder="请输入留言"></van-field>
        <van-button size="large" type="info" @click="addComment">发表评论</van-button>
      </van-cell-group>
      <!-- 评论展示模块 -->
      <van-list v-for="(item,index) in commentsList" :key="index">
        <div class="title">
          <span>第{{index+1}}楼</span>
          <span>用户:{{item.user_name}}</span>
          <span>发表时间:{{item.add_time |dateFormat()}}</span>
        </div>
        <div class="body">{{item.content}}</div>
      </van-list>
      <van-button plain hairline type="danger" size="large" @click="loadMore">加载更多...</van-button>
    </div>
    <div id="tabbar">
      <g-tabbar></g-tabbar>
    </div>
  </div>
</template>
<script>
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
export default {
  data () {
    return {
      // 评论数据对象
      commentsList: [],
      content: '',
      pageIndex: 1
    }
  },
  created () {
    this.getGoodsComments()
  },
  methods: {
    async getGoodsComments () {
      const res = await this.$http.get(
        '/api/getcomments/' +
          this.$route.params.id +
          '?pageindex=' +
          this.pageIndex
      )
      console.log(res)
      if (res.status !== 0) {
        return this.$toast('服务器请求失败')
      }
      this.commentsList = res.message
      console.log(this.commentsList)
    },
    // 点击按钮发表评论
    async addComment () {
      if (this.content.trim() === '') {
        return this.$toast('评论不能为空')
      }
      const res = await this.$http.post(
        '/api/postcomment/' + this.$route.params.id,
        { content: this.content }
      )
      console.log(res)
      if (res.status !== 0) { return this.$toast.fail('评论失败') }
      this.$toast.success('评论成功')
      this.content = ''
      this.getGoodsComments()
    },
    // 加载更多
    async loadMore () {
      this.pageIndex++
      const res = await this.$http.get(
        '/api/getcomments/' +
          this.$route.params.id +
          '?pageindex=' +
          this.pageIndex
      )
      if (res.status !== 0) {
        return this.$toast('服务器请求失败')
      }
      this.commentsList.push(...res.message)
    }
  },
  components: {
    GHeader,
    GTabbar
  }
}
</script>
<style lang="less" scoped>
.commentBox {
  padding: 0 5px;
}
.van-field {
  height: 100px;
  border: 1px solid #ccc;
  margin-bottom: 5px;
}
.van-list {
  padding: 0;
  margin-top: 10px;
  span {
    margin-right: 10px;
  }
}
.title {
  height: 30px;
  background-color: #ccc;
}
.van-button {
  margin: 5px 0;
}
</style>
